import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { ModuleRootRoutePath } from "../lazyPages";

export function BackButtonPure(
    {
        history,
        routePath = ModuleRootRoutePath
    }: any
) {
    return (
        <span style={{ position: "fixed", top: 10, left: 20, fontSize: 26 }}>
            <i
                className="zi zi_pathLeft"
                onClick={() => history.replace(routePath)}
            />
        </span>

    );
}

export const BackButton = compose(
    hot(module),
    withRouter
)(BackButtonPure);
