import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { PageScreen } from "../components/PageScreen";
import { Grid, Icon, WingBlank } from "antd-mobile";
import { withRouter } from "react-router";
import {
    CovidCovidRulePageRoutePath,
    CovidDataStatisticPageRoutePath,
    CovidHeathRegisterPageRoutePath,
    CovidHomeDiaryPageRoutePath,
    CovidNoticeListPageRoutePath,
    CovidReportMailboxPageRoutePath,
    ModuleRootRoutePath
} from "../lazyPages";
import { BackButton } from "../components/BackButton";

function createIcon(name) {
    return (
        <i
            className={`zi ${name}`}
            style={{ fontSize: 22 }}
        />
    );
}

export function HomePage(
    { history }: any
) {
    const handleNavigate = ({ route }: any) => {
        if (route) {
            history.push(route);
        }
    };

    const MainMenuList = [
        {
            text: "健康信息登记",
            route: CovidHeathRegisterPageRoutePath,
            icon: createIcon("zi_pencilalt")
        },
        {
            text: "居家日记",
            route: CovidHomeDiaryPageRoutePath,
            icon: createIcon("zi_book")
        },
        {
            text: "防控纪律",
            route: CovidCovidRulePageRoutePath,
            icon: createIcon("zi_certificate")
        },
        {
            text: "举报信箱",
            route: CovidReportMailboxPageRoutePath,
            icon: createIcon("zi_envelope")
        },
        {
            text: "纪检监督通报",
            route: CovidNoticeListPageRoutePath,
            icon: createIcon("zi_newspaper")
        },
        {
            text: "数据统计",
            route: CovidDataStatisticPageRoutePath,
            icon: createIcon("zi_chartpie")
        }
    ];


    return (
        <PageScreen>
            <BackButton />
            <img src={require("../images/疫情防控.jpg")}/>
            <br/>
            <WingBlank size="sm">
                <Grid
                    data={MainMenuList}
                    columnNum={3}
                    activeStyle={false}
                    onClick={handleNavigate}
                />
                <br/>
            </WingBlank>

        </PageScreen>
    );
}

export default compose(
    hot(module),
    withRouter
)(HomePage);
